import React from 'react'
import { hydrate } from 'react-dom'
import { Provider } from 'react-redux'
import { BrowserRouter as Router } from 'react-router-dom'
import { LocalizeProvider, localizeReducer } from 'react-localize-redux'
import ReactDOMServer from 'react-dom/server'
import configStore from './configStore'
import Application from '../shared/Application'
import { getUserLocale } from '../shared/redux/modules/helpers'

process.env.BROWSER = true

const locale = getUserLocale()

const localesInit = {
  languages: [
    { name: 'English', code: 'en' },
    { name: 'German', code: 'de' },
    { name: 'Russian', code: 'ru' },
  ],
  translation: {
    maps: {
      markerPosition: ['You are here.', 'Du bist hier.', ' .'],
      markerNearPosition: [
        'You are here... Maybe...',
        'Du bist hier... Vielleicht...',
        ' ... ...',
      ],
    },
    login: {
      header: ['Please, log in', 'Anmeldung', ''],
      email: ['E-mail', 'E-mail', 'E-mail'],
      password: ['Password', 'Passwort', ''],
      rememberMe: ['Remember Me', 'Erinnere dich an mich', ' '],
      signIn: ['Sign In', 'Einloggen', ''],
      signOut: ['Sign Out', 'Ausloggen', ''],
      forgotPass: ['Forgot Password?', 'Passwort vergessen?', ' ?'],
    },
    recovery: {
      header: ['Password Recovery', 'Passwort-Wiederherstellung', ' '],
      sendRequest: ['Send Request', 'Anfrage senden', ' '],
      help: [
        'Enter your email and click "Send Request" button. You will be sent a letter with detailed instructions.',
        'Geben Sie Ihre E-Mail-Adresse ein und klicken Sie auf "Anfrage senden". Sie erhalten einen Brief mit detaillierten Anweisungen.',
        '        .       .',
      ],
    },
  },
  options: {
    defaultLanguage: locale,
    renderToStaticMarkup: ReactDOMServer.renderToStaticMarkup,
  },
}

const state = window.__PRELOADED_STATE__

const store = configStore(state)

hydrate(
  <Provider store={store}>
    <LocalizeProvider initialize={localesInit}>
      <Router>
        <Application />
      </Router>
    </LocalizeProvider>
  </Provider>,
  document.querySelector('#app'),
)
